<template>
    <div class="el-table">
        <el-table
            v-loading="tableDataLoading"
            ref="table"
            :data="tableData"
            @selection-change="handleSelectionChange"
            style="width: 100%"
            :default-sort="tableSort">
            <template v-for="(tab,index) in tableDataHead">
              <!-- 勾选表头 -->
              <el-table-column
              fixed
              width="80"
              type="selection"
              :key="index"
              v-if="tab.tabType=='checkeds'"
              ></el-table-column>
              <!-- 文字表头 -->
              <el-table-column
              sortable
              :key="index"
              :prop="tab.property"
              :label="tab.name"
              :min-width="tab.width"
              v-if="tab.tabType=='text'">
              </el-table-column>
              <!-- 图片表头 -->
              <el-table-column
              sortable
              :key="index"
              :prop="tab.property"
              :label="tab.name"
              :min-width="tab.width"
              v-if="tab.tabType=='img'">
              </el-table-column>
              <!-- 自定义插槽 -->
              <slot name="tableHead" :tab="tab">
              </slot>
            </template>
            <el-table-column
            fixed="right"
            label="操作"
            width="150">
            <template slot-scope="scope">
                <el-button
                @click="peopleDetails(scope.row)"
                type="text"
                size="small">
                详情
                </el-button>
                <el-button
                @click="editPeople(scope.row)"
                type="text"
                class="del-btn"
                size="small">
                编辑
                </el-button>                                
                <el-button
                @click="deleteRow(scope.row)"
                type="text"
                class="del-btn"
                size="small">
                移除
                </el-button>
            </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5,10,20]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
        <!-- 修改弹框插槽 -->
        <slot name="editDialog"></slot>
        <!-- 创建弹框插槽 -->
        <slot name="createDialog"></slot>
    </div>
</template>
<script>
/*
    参数：当前分页pageSize 当前页currentPage 总条数total
    事件：业务刷新页面需要调用的获取数据方法@getData()
*/
export default {
  props: [],
  data () {
    return {
      tableData: null, //表格数据
      currentPage: 1, //当前页
      pageSize: 5, //当前页面分页数
      total: 0, //一共多少条数据
      checkeds: [], //多选
      tableDataLoading: false, //表格加载状态
      tableSort: {}, //表格排序对象
      tableDataHead: null, //表头配置
    };
  },
  created () {
    this.tableDataHead = this.$tableHead.get('public_test');
  },
  mounted () {

  },
  methods: {
    getData (page = { pageSize: this.pageSize, pageNo: this.currentPage, type: "" }) {

    },
    handleSizeChange (pageSize) {
      //分页个数事件
    },
    handleCurrentChange (Current) {
      //翻页事件
    },
    handleSelectionChange (val) {
      //多选事件
      if (val.length) {
        this.checkeds = val;
      } else {
        this.checkeds = null;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.el-table {
  width: 100%;
  .pagination {
    text-align: center;
  }
}
</style>



